'use strict';

angular.module('inspinia')
  .controller('UsersController', ['bdAPI', '$scope', 'aws', 'DTColumnDefBuilder', function (bdAPI, $scope, aws, DTColumnDefBuilder) {


		
		console.log(bdAPI);

		console.log("let's get some more users...");
    var vm = this;
    
	
    
  		//Get users
		vm.pagination = {
			usersPerPage:20,
			prev:false,
			next:false,
			nextURL:"",
			page:1,
			loaded:0
		};
		vm.pagination.startIndex = (vm.pagination.page-1)*vm.pagination.usersPerPage;
		
		vm.findIndex = function(userId){
	    for(var i = 0; i < vm.users.length; i++) {
	      if(vm.users[i].userId === userId) return i;
	    }
	    return -1;
		}
		
		function incrementPage(inc){
			vm.pagination.page = vm.pagination.page+inc;
			vm.pagination.startIndex = (vm.pagination.page-1)*vm.pagination.usersPerPage;
			if (inc > 0) vm.pagination.prev=true;
			else vm.pagination.next=true;
		}
		
		
		vm.pagination.changePage = function(dir){
			
			if (vm.pagination.page == 1 && dir=="prev") return false;
			if (vm.pagination.page == 2 && dir=="prev") vm.pagination.prev=false;
			
			//put in rules for the last page
			
			if (dir =="next"){
				if (vm.pagination.page < vm.pagination.loaded) incrementPage(1);
				else{
					pullusers(function(){
						incrementPage(1);
					})
				}
			}else{
				incrementPage(-1);				
			}	
		}
		
		

		function pullusers(callback){
			var args = vm.pagination.nextURL ? [vm.pagination.usersPerPage, vm.pagination.nextURL] : vm.pagination.usersPerPage;
			bdAPI.call('usersList', args, function(result){
				console.log(result);
				vm.users.push.apply(vm.users, result.data.items);
				if (result.data.next){
					vm.pagination.nextURL=result.data.next;
					vm.pagination.next = true;
				}
				else vm.pagination.next = false;
				vm.pagination.loaded++;	
				if (callback){
				 callback();
				}
				$scope.$apply();
			});
		}
		
		//Init users
		vm.users = [];
		pullusers();    
    
   $scope.dtColumnDefs = [
   	DTColumnDefBuilder.newColumnDef(3).notSortable(),
	 	DTColumnDefBuilder.newColumnDef(2).notSortable()
	 ];
    		
        


		

    vm.userName = 'Example user';
    vm.helloText = 'Welcome in INSPINIA Gulp SeedProject';
    vm.descriptionText = 'It is an application skeleton for a typical AngularJS web app. You can use it to quickly bootstrap your angular webapp projects.';

  }]);


(function() {

  angular
    .module('inspinia')
    .service('bdAPI', bdAPI);

  bdAPI.$inject = ['$window', '$http', '$httpParamSerializer', '$filter','$q', 'aws'];
  function bdAPI ($window, $http, $httpParamSerializer, $filter, $q, aws) {

	  $window._thirdParty = $window._thirdParty || {};
	  $window._thirdParty.BlueDeltaApi =new API.Client.DefaultApi(angular.injector(["ng"]).get("$http"),
    angular.injector(["ng"]).get("$httpParamSerializer"),
    angular.injector(["ng"]));

    var BlueDeltaApi = $window._thirdParty.BlueDeltaApi;
		
		//JSON Data
		BlueDeltaApi.jsonData = {};
		setupJsonData = function(key){
			$http.get('/data/'+key+'.json').then(function(response){
				BlueDeltaApi.jsonData[key] = response.data;
			});
		}
		
		jsonDataKeys = [
			'chris-jeans',
			'profile',
			'styles',
			'threads',
			'buttons',
			'fabrics',
			'genders',
			'tailors'
		];
		
		for (d = 0; d < jsonDataKeys.length; d++) { 
			setupJsonData(jsonDataKeys[d]);
		}
		
		BlueDeltaApi.getJsonData = function(){
			return this.jsonData;
		}
		var styleByGender = function(gender){
			return function(style){
				return style["images_"+gender];
		  }
		};
		
		BlueDeltaApi.jeanKeyToDataKey = function(jeanKey){
			var dataKey = false;
			
			switch(true){
				case jeanKey.indexOf('thread') > -1:
				dataKey = "threads";
				break;
				
				case (jeanKey == "gender" || jeanKey == "style" ||  jeanKey == "fabric"):
				dataKey = jeanKey+"s";
				break;
				
			}
			return  dataKey;
		}
			
				
		BlueDeltaApi.dataLookup = function(dataKey, id, attr){
			
			attr = attr||null;
	
			if (typeof dataKey == 'undefined' || typeof id == 'undefined') return false;
			var dataSet = BlueDeltaApi.jsonData[dataKey];
			dataById = 	$filter('filter')(dataSet, {id: id});
			if (typeof dataById == 'undefined') return false; //Return false when dataSet is undefined...
			
			selected = dataById[0];
			
			if (!attr) return selected;
			else return selected[attr];	
			
		}

		var cleanUser = function(user){
			var temp = JSON.stringify(user);
			temp = temp.replace(/\"\"/g, null);
			user = JSON.parse(temp);
			return user;
		}
				
				
		BlueDeltaApi.setupHeaders = function(){
			var userData = aws.getCurrentUserFromLocalStorage();
			this.defaultHeaders_['Authorization'] = userData.idToken.getJwtToken();
			var idTokenPayload = userData.idToken.jwtToken.split('.')[1];
			var identityID = JSON.parse(atob(idTokenPayload)).sub;	
			return identityID;
		}
		
		BlueDeltaApi.call = function(func,args,success,error){
			var bdapi = this;
	    aws.getCurrentUserFromLocalStorage(function(session){
		    //Reset auth header
				bdapi.defaultHeaders_['Authorization'] = session.idToken.getJwtToken();
				
				//put args into array form
				if (!Array.isArray(args)) args = [args];
				
				bdapi[func].apply(bdapi, args).then(function(result){
					success(result);
				}, function(err){
					console.log(err);
					if (error) error(err);
				});
	    });
		}

		//Save User
		BlueDeltaApi.saveUser = function(user){
			var defer = $q.defer();
			user = cleanUser(user);
			if (!user.identityId) defer.reject("No identity Id");
			else{
				BlueDeltaApi.usersUpdate(user.identityId, user).then(
					function(result){
						console.log("got it");
						defer.resolve(result);
					},
					function(err){
						console.log("Error saving user:");
						console.log(err);
						defer.reject(err);
					}	
				)
			}
			return defer.promise;	
		}

	  return BlueDeltaApi;
  }

})();
